@use '../../../styles/_variables' as *;

.card {
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px,
    rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
  border-radius: 20px;
  padding: 20px;
  display: flex;
  min-height: 40%;
  overflow: hidden;
  background-color: white;
}
